<template>
    <div id="person" style="height: 100%;">
        <ul style="margin-top: 10px;">
            <li class="title">
                <input type="text" placeholder="请输入标题" v-model="title">
            </li>
            <li class="arttype">
                <span><input type="radio" value="1" id="jishu" v-model="arttype" name="arttype"><label for="jishu">技术</label> </span>
                <span><input type="radio" value="2" v-model="arttype" id="manshenghuo" name="arttype"><label for="manshenghuo">慢生活</label></span>
            </li>
            <li class="des">
                <div>文章描述</div>
                <textarea v-model="des"></textarea>
            </li>
            <li>
                <editor id="editor_id" height="700px"  width="1000px" :content="editorText"
                        pluginsPath="/static/kindeditor/plugins/"
                        :loadStyleMode="false"
                        @on-content-change="onContentChange"
                        :uploadJson="uploadJson"
                        :resizeType="resizeType"
                ></editor>
            </li>
            <li class="imgurl">
                <div class="imgurlTitle">文章封面</div>
                <div class="preImg">
                    <img :src="preSrc" alt="">
                    <input type="file" accept="image/*" @change="imgurlupload">
                </div>
            </li>
            <li class="Btn">
                <span @click="fabiao">
                    {{pinglunBtn}}
                </span>
            </li>
        </ul>
        <div id="welcomeMask" v-show="showloading">
            <img src="/static/img/carloading.0f155b9.gif"  style="width: 300px;">
        </div>
    </div>
</template>

<script>
    import status from '../../common/status.js'
    export default {
        name: "personal",
        data () {
            return {
                editorText: '',
                title:'',
                des:'',
                arttype:1,
                uploadJson:'/api/upload',
                resizeType:0,
                pinglunBtn:'发表',
                preSrc:'/static/img/s1.876a7ff.jpg',
                showloading:false
            }
        },
        created () {
        },
        methods: {
            onContentChange (val) {
                this.editorText = val.replace(/\'/g,'"')
            },
            fabiao(){
                if(status.get('userName')){
                    this.pinglunBtn='发表'
                    var userid=status.get('userid');
                    var username=decodeURIComponent(status.get('userName'));
                    var time=new Date().getTime();
                    var _this=this;
                    if(_this.title==0){
                        alert('请输入标题')
                    }else{
                        _this.showloading=true
                        this.$http.post('/api/fabiao',{zhan:0,title:_this.title,text:_this.editorText,des:_this.des,arttype:_this.arttype,userid:userid,username:username,time:time,imgurl:_this.preSrc}).then(data=>{
                            _this.$root.eventHub.$emit('shuaxin',null)
                            _this.title='';
                            _this.editorText='';
                            _this.des='';
                            _this.showloading=false
                            alert('发表成功')
                        })
                    }
                }
                else{
                    this.pinglunBtn='请先登录'
                }
            },
            imgurlupload(){
                let file = event.target.files[0];
                var _this=this
                console.log(file.name)
                //如果是图片文件
                let reader = new FileReader();
                let imageUrl = reader.readAsDataURL(file);
                reader.onload = function(e) {
                    var url = reader.result;
                    _this.preSrc = url;
                };
                let formData = new FormData();
                formData.append('file', file);
                this.$http.post('/api/upload',formData).then(data=>{
                    _this.preSrc='/static/img/'+file.name
                })
            }
        }
    }
</script>

<style lang="less" rel="stylesheet/less" scoped>
#person{
    width: 1000px;
    margin: 0 auto;
    .header{
        text-align: center;
        color: white;
        font-size: 20px;
        padding: 20px 0px;
    }
    .title{
        width: 100%;
        input{
            background: #333;
            outline: none;
            width: 100%;
            text-align: center;
            font-size: 24px;
            height: 30px;
            color: white;
        }
        input::placeholder{
            color: #666;
        }
    }
    .arttype{
        color: white;
        text-align: center;
        margin-top: 10px;
        input{
            vertical-align: bottom;
            margin-right: 2px;
            cursor: pointer;
        }
        span{
            margin-right: 10px;
        }

    }
    .des{
        div{
            color: #fafafa;
            padding: 30px 0px 10px 0px;
            font-size: 20px;
            text-indent: 10px;
        }
        textarea{
            resize: none;
            width: 100%;
            height: 200px;
            font-size: 20px;
            outline: none;
            border-radius: 5px;
        }
    }
    .imgurl{
        .imgurlTitle{
            color: #fafafa;
            padding: 20px 0px 10px 0px;
            font-size: 20px;
            text-indent: 10px;
        }
        .preImg{
            position: relative;
            width: 400px;
            height: 200px;
            background: #fff;
            padding: 4px;
            border-radius: 6px;
            box-shadow: 0px 1px 0px rgba(255, 255, 255, 0.1), inset 0px 1px 2px rgba(0, 0, 0, 0.7);
            img{
                width: 400px;
                height: 200px;
                position: absolute;
                left: 4px;
                top: 4px;
                border-radius: 3px;
            }
            input[type=file]{
                position: absolute;
                width: 100%;
                height: 100%;
                outline: none;
                opacity: 0;
            }
        }
    }
    .Btn{
        margin: 20px 0px;
        text-align: center;
        span{
            display: inline-block;
            cursor: pointer;
            width: 90px;
            height: 30px;
            line-height: 30px;
            background: #3399ff;
            border-radius: 4px;
            color: white;
        }
    }
    #welcomeMask{
        position: fixed;
        width: 100%;
        height: 100%;
        z-index: 1000;
        background: #000000;
        left: 0;
        top: 0;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
</style>


